import React from 'react';
import Sdata from './Sdata';
// import web from '../Image/thumb_1.png';
import Card from './Card';

const Service = () => {
    return (
        <>
            <section id="service" className="">
                <div className="my-5">
                    <h1 className="text-center">Our Services</h1>
                </div>
                    <div className="row">
                        <div className="col-10 mx-auto">
                            <div className="row">
                                {Sdata.map((val, ind) => {
                                    return <Card key={ind} imgsrc={val.imgsrc} title={val.title} describe={val.describe}/>
                                })}
                            </div>
                        </div>
                    </div>

            </section>
        </>
    )
}

export default Service;