import React from 'react'
import { useState } from 'react';


const Contact = () => {
    const [data, setData] = useState({
        fullname: "",
        mobile: "",
        email: "",
        message: ""
    })
   

    const inputEvent = (event) => {
        const { name, value } = event.target;

        setData((preVal) => {
            return {
                ...preVal,
                [name]: value,
            };
        });
    }

    const formSubmit = (e) => {
        e.preventDefault();
        alert(`Myself ${data.fullname}, mobile ${data.mobile}, mail id ${data.email} Purpose of Contacting ${data.message}`)
    }
    return (
        <section id="contact" className="">
            <div className="container">
                <div className="row">
                    <div className="col-md-6 col-10 mx-auto mt-5">
                        <h1 className="text-center">Contact Us</h1>
                        <form onSubmit={formSubmit}>
                            <div className="form-group">
                                <label htmlFor="fullname">Full Name</label>
                                <input type="text" className="form-control" id="fullname" name="fullname" value={data.fullname}
                                    onChange={inputEvent} aria-describedby="emailHelp" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="mobile">Mobile Number</label>
                                <input type="number" className="form-control" id="mobilenumber" name="mobile" value={data.mobile}
                                    onChange={inputEvent} aria-describedby="emailHelp" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="email">Email</label>
                                <input type="email" className="form-control" id="email" name="email" value={data.email}
                                    onChange={inputEvent} aria-describedby="emailHelp" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="message">Message</label>
                                <input type="text" className="form-control" id="message" name="message" value={data.message}
                                    onChange={inputEvent} />
                            </div>
                            <button type="submit" className="btn btn-outline-primary">Submit</button>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Contact